import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { ApicallsProvider } from '../../providers/apicalls/apicalls';
import { Geolocation } from '@ionic-native/geolocation';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  public geoapi:any;
  public latitude;
  public longitude;
  public errror:any;

  constructor(private geolocation: Geolocation,public navCtrl: NavController,public APICall: ApicallsProvider) {
    this.geoapi='http://www.apiboutique.com/geoservice/capture.php';
  }

  startservice() {
    console.log("THanks");
   // let userdata='userid='+this.APICall.getlocaldata('USERID')+'&childid='+childid+'&token='+this.APICall.getlocaldata('TOKEN');
   let geodata='';

  
  
  this.geolocation.getCurrentPosition().then((resp) => {
    // resp.coords.latitude
    // resp.coords.longitude
   }).catch((error) => {
     console.log('Error getting location', error);
     this.errror='Error getting location'+error;
   });
   
   let watch = this.geolocation.watchPosition();
   watch.subscribe((data) => {
    // data can be a set of coordinates, or an error (if an error occurred).
    // data.coords.latitude
        // data.coords.longitude
        console.log(data);
        this.latitude=data.coords.latitude;
        this.longitude=data.coords.longitude;
        geodata='lat='+this.latitude+'&long='+this.longitude;
        this.APICall.getDataByPost(this.geoapi,geodata).then(data => {
          console.log(data);
        });

   });

  }

}



